import { csvParse, autoType } from "d3-dsv";

const endpoint = 'https://ftb-api-ext.ons.sensiblecode.io/graphql';
const frag = `
fragment tableDimensions on Table {
  dimensions {
    count
    categories {
      code
      label
    }
    variable {
      name
      label
    }
  }
}
`.replace(/\s+/g, " ");
const credentials = btoa("ahmad.barclay"+":"+"elope.puck.hails.explore");
const headers = new Headers({
  "Content-Type": "application/json",
  "Authorization": "Basic " + credentials
});

// new Headers(JSON.parse(atob(headers)))

export async function getData(options, keys, variables = []) {
  const string = variables[0] ? '"' + variables.join('","') + '",' : '';
  let filters = [];

  keys.forEach(key => {
    if (variables.includes(options[key].code)) {
      filters.push(`{variable: "${options[key].code}", codes: ${'["' + options[key].options.filter(d => d.code != null).map(d => d.code).join('","') + '"]'}}`)
    }
  });

  filters = filters[0] ? '[' + filters.join(',') + ']' : '[]';

  const query = `
  query {
    dataset(name:"Usual-Residents") {
      health: table(
        variables: [${string}"HEALTH"]
        filters: ${filters}
      )
      {
        ...tableDimensions
        values
      }
      employment: table(
        variables: [${string}"ECOPUK11_R006A"]
        filters: ${filters}
      )
      {
        ...tableDimensions
        values
      }
      education: table(
        variables: [${string}"HLQPUK11_R005A"]
        filters: ${filters}
      )
      {
        ...tableDimensions
        values
      }
    }
  }
  `.replace(/\s+/g, " ");

	const ops = {
		body: JSON.stringify({
			"query": query + frag
		}),
		headers: headers,
		method: "POST",
		mode: "cors"
	};
	
	let response = await fetch(endpoint, ops);
  let json = await response.json();
  return json;
}

export async function getGeo(options, selected, keys, variables = []) {
  const string = variables[0] ? ',"' + variables.join('","') + '"' : '';
  let filters = [];

  keys.forEach(key => {
    if (variables.includes(options[key].code)) {
      filters.push(`{variable: "${options[key].code}", codes: ${'["' + selected[key].code + '"]'}}`)
    }
  });

  filters = filters[0] ? '[' + filters.join(',') + ']' : '[]';

  console.log(options, keys, variables, filters);

  const query = `
  query {
    dataset(name:"Usual-Residents") {
      table(
        variables: ["MSOA"${string}]
        filters: ${filters}
      )
      {
        ...tableDimensions
        values
      }
    }
  }
  `.replace(/\s+/g, " ");

	const ops = {
		body: JSON.stringify({
			"query": query + frag
		}),
		headers: headers,
		method: "POST",
		mode: "cors"
	};
	
	let response = await fetch(endpoint, ops);
  let json = await response.json();
  return json;
}

export async function getCSV(url) {
  let response = await fetch(url);
  let string = await response.text();
  let json = csvParse(string, autoType);
  return json;
}

export function getColor(value, breaks, colors) {
  for (let i = 1; i < breaks.length; i ++) {
    if (value <= breaks[i]) {
      return colors[i - 1];
    }
  }
}